import axios from "axios";
import qs from "qs";

const instance = axios.create({
    timeout: 10000
})

export default instance

instance.interceptors.request.use(
    config => {
        if (config.method === 'post') {
            config.data = qs.stringify(config.data)

        }
        console.log('=== requestUrl:' + config.url + config.method === 'post' ? ('\n=== postData:' + config.data) : '')
        return config
    },
    error => {
        console.log('=== requestUrl:' + config.url + '\m=== error:' + error)
        return Promise.reject(error)
    }
)

instance.interceptors.response.use(
    response => {
        console.log('=== response \n' + JSON.stringify(response.data))
        if (response.status === 200 && response.data != undefined) {
            return Promise.resolve(response.data)

        } else {
            return Promise.reject(response)
        }
    },
    error => {
        console.log('=== response error,status:' + error.code + 'info:' + error.message)
        return Promise.reject(error)

    }
)