<template>
    <div class="container">
        <div class="banner-container" :style="{ transform: `translateX(${-currentIndex * 100}%)` }"
            @touchstart="handleTouchStart" @touchend="handleTouchEnd">
            <div class="banner-item-container" v-for="(item, index) in prop.dataArray" :key="index"
                @click="handleClick(index)">
                <img :src="item.picUrl" class="banner-img">
                <div class="banner-item-title-container">
                    <div class="banner-item-title">{{ item.title }}</div>
                </div>
            </div>
        </div>
    </div>
</template>

<script setup>
import { ref } from 'vue'
const prop = defineProps({
    dataArray: {
        type: Array,
        Required: true
    }
})
const emit = defineEmits(['didSeletc'])
function handleClick(index) {
    emit('didSeletc', index)
}

const currentIndex = ref(0)
const currentUrl = ref(prop.dataArray[0].picUrl)


function toPrevious() {
    if (currentIndex.value == 0) {
        currentIndex.value = prop.dataArray.length - 1

    } else {
        currentIndex.value--
    }
}

function toNext() {
    if (currentIndex.value == prop.dataArray.length - 1) {
        currentIndex.value = 0

    } else {
        currentIndex.value++
    }
}

let startX = 0;
let startY = 0;
function handleTouchStart(event) {
    startX = event.touches[0].clientX;
    startY = event.touches[0].clientY;
}

function handleTouchEnd(event) {
    const endX = event.changedTouches[0].clientX;
    const endY = event.changedTouches[0].clientY;
    const gapX = endX - startX;
    const gapY = endY - startY;
    if (Math.abs(gapX) > Math.abs(gapY)) {
        if (gapX > 50) {
            toPrevious();
        } else if (gapX < -50) {
            toNext()
        }
    }
}
</script>

<style scoped>
.banner-item-title-container {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 22px;
    background-color: rgba(111, 111, 111, 0.6);
}

.banner-item-title {
    text-align: center;
    width: 100%;
    height: 100%;
    color: white;
    font-size: 12px;
    line-height: 22px;
}

.banner-container {
    width: 100%;
    height: 100%;
    display: flex;
    transition: transform 0.5s ease-in-out;
}

.banner-item-container {
    display: flex;
    height: 100%;
    /* min-width: 100%; */
    width: 100%;
    flex-shrink: 0;
    position: relative;
    border-radius: 12px;
    /* overflow: hidden; */
}

.banner-img {
    height: 100%;
    width: 100%;
    object-fit: cover;
}

.bottom-container {
    position: absolute;
    bottom: 0;
    z-index: 1000;
}

.container {
    height: 112px;
    overflow: hidden;
    position: relative;
}
</style>