<template>
    <navgation :navititle="currentNavTitle"></navgation>
    <banner :dataArray="dataArray" @didSeletc="emitDidselect"></banner>
    <!-- <div @click="indexTest">index++测试</div> -->
    <div>
        <div class="file-container" v-for="(file, index) in fileList" :key="file.link"
            @click="linktoContent(file, index)">
            <img class="file-icon" :src="srcUrl" alt="PDF Icon" @error="onError" @load="onLoad">
            <div class="file-info-container">
                <div class="file-info-title">{{ file.name }}</div>
                <div class="file-info-subtitle">{{ file.link }}</div>
            </div>
        </div>
    </div>
</template>

<script setup>
import { onMounted, reactive, ref } from 'vue'
import request from '@/request/request'
import navgation from '@/components/navigation.vue'
import { useRoute, useRouter } from "vue-router"
import { useFileStore } from "@/stores/file";
const fileStore = useFileStore()

const router = useRouter()
const route = useRoute()

const fileList = reactive([])
const currentNavTitle = ref('文件列表')

import srcUrl from '@/assets/pdf_icon.png'

function indexTest() {
    fileStore.userAdd()
    fileStore.file.title += '222'
}

function linktoContent(file, index) {
    fileStore.file.index = index
    router.push({ path: '/pdfview', query: file })
}

//banner 
import banner from '@/components/banner.vue'
const dataArray = ref([
    {
        title: '徐民高速单曹段建设按下“加速键”',
        link: 'www.baidu.com',
        picUrl: require('@/assets/news1.png'),
    },
    {
        title: '日兰高铁全线贯通运营',
        link: 'www.baidu.com',
        picUrl: require('@/assets/news2.png'),
    },
    {
        title: '雄商高铁站前十三标首架方向箱梁架设完成',
        link: 'www.baidu.com',
        picUrl: require('../assets/news3.png'),
    }
])
function emitDidselect(index) {
    // console.log(index)
}

onMounted(() => {
    request.getFileList().then((result) => {
        fileList.push(...result.data)
        fileStore.file.fileList = fileList
    }).catch((err) => {
        console.log(err)
    });
})

</script>

<style scoped>
.file-container {
    margin-top: 10px;
    margin-left: 12px;
    margin-right: 12px;
    height: 77px;
    border-radius: 6px;
    background: rgba(255, 255, 255, 1);
    display: flex;
    flex-direction: row;
    align-items: center;
}

.file-icon {
    width: 52px;
    height: 52px;
    margin-left: 18px;
    /* background-color: black; */
}

.file-info-container {
    margin-left: 15px;
    display: flex;
    flex-direction: column;
    width: 70%;
    height: 100%;
}

.file-info-title {
    margin-top: 17px;
    color: rgba(51, 51, 51, 1);
    text-align: left;
    vertical-align: top;
    font-size: 16px;
    font-weight: 500;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
}

.file-info-subtitle {
    margin-top: 8px;
    font-size: 14px;
    font-weight: 400;
    color: rgba(153, 153, 153, 1);
    text-align: left;
    vertical-align: top;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
}
</style>